/* Customize the form container */
.d-flex.searchForm {
    width:300px; /* Set the width of the form */
    max-width: 400px; /* Set the maximum width for the search bar */
    margin: auto; /* Center the search bar */
  }
  
  /* Style the search input */
  .form-control.searchInput {
    border: 2px solid #BC6C25; /* Border color */
    border-radius: 4px; /* Rounded corners */
    padding: 8px; /* Padding inside the input */
    margin-right: 5px; /* Margin between input and button */
    font-size: 14px; /* Font size */
    transition: border-color 0.3s ease-in-out; /* Smooth transition for border color */
  }
  
  /* Style the search button */
  .btn.searchButton {
    background-color: #BC6C25; /* Background color */
    border: 2px solid #BC6C25; /* Border color */
    border-radius: 4px; /* Rounded corners */
    color: #ffffff; /* Text color */
    font-size: 14px; /* Font size */
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out; /* Smooth transition for background color and text color */
  }
  
  /* Hover styles for the search button */
  .btn.searchButton:hover {
    background-color: #ffffff; /* Change background color on hover */
    color: #BC6C25; /* Change text color on hover */
  }
  
  /* Focus styles for the search input */
  .form-control.searchInput:focus {
    outline: none; /* Remove the default focus outline */
    border-color: rgb(149, 128, 99); /* Change border color on focus */
  }
  