
/* REGISTER PAGE CSS  */
.form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 70vh;
    font-family: 'Poppins', sans-serif;   
     }

  .auth-layout{
    background-image: url("../../public/images/bg-1.jpg");
    background-size:contain;
    background-repeat: no-repeat;
   
  }

  .form-container form{
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 20px;
    background-color: white;
   
  }

  .form-container .form-title{
    text-align: center;
    font-size: 40px;
    font-weight: bold;
  
    margin-bottom: 15px;
    color: rgb(112, 91, 64);
  }
  
  .form-container label{
    font-size: 15px;
    font-weight: 400;
    color: rgb(112, 91, 64);
  
  }
  
  .form-container input, .form-container textarea{
   
    width: 300px;
    border: none;
    border-bottom: 1px solid rgb(112, 91, 64);
    border-radius: 0;
    font-size: 15px;
    font-weight: 300;
  }
  
  .form-container input:focus, .form-container textarea:focus{
    outline: none;
    border-bottom: 1px solid rgb(112, 91, 64);
  }
  
  .form-container button{
    padding: 5px 20px;
    border-radius: 5px;
    color: white;
    border: 1px solid rgb(112, 91, 64);
    background-color: rgb(112, 91, 64);
    font-size: 20px;
    font-weight: 400;
    cursor: pointer; 
    width: 100%;
  }
  
  .form-container button:hover{
    background-color: transparent;
    color: rgb(112, 91, 64);
    border: 1px solid rgb(159, 139, 113);
  }
  
  @media screen and (max-width: 768px){
  
    .form-container label{
      display: none;
    }
    
  }