@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
/* font-family: 'Nunito Sans', sans-serif;
font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif; */


/*
color palette
1. Sandy brown : #F48E52
2. Corn: #EEB504
3. Brick Red : #D72E44 , E83963
4. Saffron Mango: #FCCB4C

II Color Palette
1. Light Olive: #606C38
2. Dark Olive: #283618
3. Cream : #FEFAE0
4. Light Brown : #DDA15E
5. Dark Brown : #BC6C25

*/
:root{
  --light-olive: #606C38;
  --lighter-olive:#8bdc80;
  --dark-olive: #283618;
  --cream: #FEFAE0;
  --light-brown: #DDA15E;
  --dark-brown: #BC6C25;
}

*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  
}

/* HEADER JS PAGE,  NAVBAR CSS  */

.navbar{
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow: 0px 8px 6px -6px gray;
  border-bottom: 0 solid #e6e6e6 !important;
 
}

.nav-link{
  font-weight: 300 !important;
  color: var(--dark-brown) !important;
}

.active{
  border-bottom: 2px solid var(--dark-olive) !important;
}

.navbar-brand{
  font-family: 'Roboto', 'sans-serif';
  font-size: 25px;
  font-weight: 700;
  letter-spacing: 3px;
}

/* FOOTER CSS */
.footer{
  color: white;
  padding:  25px 0;
  background: var(--dark-olive);
background: linear-gradient(260deg, var(--dark-olive) 0%, var(--dark-olive) 48%, black 100%);
  
}

.footer a{
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover{
  color: var(--light-brown);
  border-bottom: 1px solid var(--cream);
}

/* PAGE NOT FOUND CSS  */
.pnf{
  display: flex;
  min-height: 50vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title{
  font-size: 100px;
  font-weight: 700;
}

.pnf-subtitle{
  font-size: 30px;
  font-weight: 300;
}

.pnf-btn{
  margin-bottom: 15px;
  padding: 10px 20px;
  border-radius: 5px;
  color: black;
  border: 1px solid black;
  background-color: transparent;
  font-size: 20px;
  font-weight: 300;
  cursor: pointer; 
}

@media screen and (max-width: 768px){
  .pnf-title{
    font-size: 70px;
  }
  .pnf-subtitle{
    font-size: 20px;
  }

  .pnf-img{
    display: none;
  }

  .pnf-btn{
    font-size: 15px;
  }
  
}

/* CONTACT US PAGE CSS */
.contactus{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  min-height: 60vh;
}

.contact-details h2{
  font-size: 50px;
  font-weight: 700;
  margin-bottom: 20px;
  color:var(--dark-brown);
}

.contact-details h3{
  font-size: 30px;
  font-weight: 300;
  margin-bottom: 20px;
  color: rgb(159, 139, 113);
}

.contact-details h3 strong{
  color: rgb(109, 91, 74);
  font-weight: 600;
}

.contact-details h3 .email-contact{
  text-decoration: none;
  color: rgb(159, 139, 113);
  font-weight: 300;
}

.contact-details h3 .email-contact:hover{
  color: rgb(109, 91, 74);

}

@media screen and (max-width: 1032px){
  .contactus{
    flex-direction: column;
  }

  .contact-details{
    margin-bottom: 20px;
  }

  .contact-details h2{
    font-size: 40px;
  }

  .contact-details h3{
    font-size: 20px;
  }

}

@media screen and (max-width: 768px){

  .contact-image{
    display: none;
  }

}

/* ABOUT US PAGE CSS */
.aboutus{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 20px;
  min-height: 80vh;
}
.aboutus-content{
  padding: 0 20px;
  width: 40%;
}


.aboutus-content h2{
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--dark-brown);
}

.aboutus-content h4{
  font-size: 20px;
  font-weight: 400;
  margin-bottom: 20px;
  color: var(--light-brown);
}

.aboutus-content p{
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
  color: var(--dark-olive);
}

@media screen and (max-width: 1032px){
  .aboutus{
    flex-direction: column;
  }

  .aboutus-content{
    width: 100%;
  }

  .aboutus-content h2{
    font-size: 30px;
  }

  .aboutus-content h4{
    font-size: 15px;
  }

  .aboutus-content p{
    font-size: 15px;
  }
  
}

/* PRIVACY POLICY PAGE CSS */

.policy-header{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 20px;
  min-height: 10vh;
}

.policy-header h1{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
  color: rgb(98, 84, 66);
}

.policy-header p{
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
  color: var(--dark-olive);

}

.privacy-policy{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  padding: 20px;
  min-height: 80vh;
}

.privacy-policy-content{
  padding: 0 20px;
  width: 40%;
}

.privacy-policy-content h1{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
  color: rgb(98, 84, 66);
}

.privacy-policy-content h2{
  font-size: 40px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--dark-brown);
}

.privacy-policy-content h3{
  font-size: 30px;
  font-weight: 500;
  margin-bottom: 20px;
  color: var(--dark-brown);
}

.privacy-policy-content h4{
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--light-brown);
}

.privacy-policy-content  h5{
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 20px;
  color: var(--light-brown);
}

.privacy-policy-content  p{
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 20px;
  color: var(--dark-olive);
}

.privacy-policy-content li{
  font-size: 18px;
  font-weight: 400;
  color: var(--light-olive);
}

@media screen and (max-width: 1032px){
  .privacy-policy{
    flex-direction: column;
  }

  .privacy-policy-content{
    width: 100%;
  }

  .privacy-policy-content h1{
    font-size: 30px;
  }

  .privacy-policy-content h2{
    font-size: 30px;
  }

  .privacy-policy-content h3{
    font-size: 20px;
  }

  .privacy-policy-content h4{
    font-size: 20px;
  }

  .privacy-policy-content h5{
    font-size: 15px;
  }

  .privacy-policy-content p{
    font-size: 15px;
  }

  .privacy-policy-content li{
    font-size: 15px;
  }
  
}

@media screen and (max-width: 768px){

  .privacy-policy-content img{
    display: none;
  }
  
}

/* ALL CATEGORY PAGE CSS  */
.cat-btn:hover {
  background-color: var(--lighter-olive) !important ;
  border-color: var(--lighter-olive) !important ;
  color: #177715 !important ;
  transition: 0.5s ease-in-out;
  transform: scale(1.02) ;
}

/* ORDER PAGE CSS  */
.order-card:hover{
  box-shadow: 0px 8px 6px -6px gray;
  --webkit-box-shadow: 0px 8px 6px -6px gray;
  border: 2px solid var(--dark-olive);
}

/* USERMENU CSS */
 .userprofile-item.active , .adminmenu-item.active{
  background-color: var(--light-brown) !important ;
  border-color: var(--light-brown) !important ;
  color: var(--dark-olive) !important ;
  font-weight: bold !important;
} 